document.addEventListener('DOMContentLoaded', function () {
  const radioButtons = document.querySelectorAll('.custom-checker__input');
  const items = Array.from(document.querySelectorAll('.services__item'));

  if (radioButtons.length === 0 || items.length === 0) {
    return;
  }

  function filterItems() {
    const selectedCategory = document.querySelector(
      '.custom-checker__input:checked'
    ).value;

    items.forEach(item => {
      item.classList.add('services__item--hide');
    });

    setTimeout(() => {
      items.forEach(item => {
        if (
          selectedCategory === 'all' ||
          item.classList.contains(selectedCategory)
        ) {
          item.style.display = 'flex';
          setTimeout(() => item.classList.remove('services__item--hide'), 0);
        } else {
          item.style.display = 'none';
        }
      });
    }, 500);
  }

  radioButtons.forEach(radio => {
    radio.addEventListener('change', filterItems);
  });
});
